</template>
<script>
	import https from '../api/https'
	export default {
		name: 'VideoRental',
		data: function(){
			return {
				bgWrapHeight: 200,
                moneySymbol: https.moneySymbol,
                fixNumber: https.fixNumber,
                videoList: [],
                about: '',
                refresh: 0,
                canRefresh: true,
                selectVideoId: '',
                selectVideoItem: {},
                charts: {
                    columns: [],
                    values: []
                },
                showUpgreadAlert: false,
                showSelectModel: false,
                selectLevel: 1,
                timer: null,
                requireLevel: '',
                myLevel: '',
                showPlayVideo: false,
                rentSuccess: false,
                showUpgreadAlert2: false,
                showBalanceAlert: false,

                remainingPost: '',
                isBlack: '',
                balance: '',
			}
		},
		created(){
			this.getData()

            if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
		},
		methods: {
			getData: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);

				https.fetchGet('/api/video/rent/recommend').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.videoList = res.data.videos
                        this.about = res.data.about
                        this.refresh = res.data.refresh
                        this.myLevel = res.data.myLevel
                        this.remainingPost = res.data.remainingPost
                        this.isBlack = res.data.isBlack
                        this.balance = res.data.balance
                        
                        if(this.refresh/1 > 0) {
                            this.timer = setInterval(() => {
                                if(this.refresh/1 > 0) {
                                    this.refresh = (this.refresh/1) - 1
                                }else  {
                                    clearInterval(this.timer)
                                }
                            }, 1000);
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            goRental(item){
                if(this.isBlack == true) {
                    this.$toast.loading({
                        message: 'Video has been\nrented out',
                        forbidClick: true,
                        duration: 2000,
                        className:'smallLoading'
                    });
                }else if(this.remainingPost <= 0) {
                    this.showUpgreadAlert2 = true
                }else if(this.balance/1 < 0) {
                    this.showBalanceAlert = true
                }else if(item.rentalFeeAdv) {
                    this.showSelectModel = true
                }else {
                    this.videoRental(item.id)
                }
            },
            videoRental: function(id){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'rent',
                    product: id,
                }
				https.fetchPost('/api/pay/cashier/create',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.rentSuccess = true
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            clickRefresh(){
                if(this.refreshTime === '0') {
                    this.refreshVideo()
                }
            },
            refreshVideo: function(){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
				https.fetchGet('/api/video/rent/refresh').then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
						this.videoList = res.data.videos
                        this.refresh = res.data.refresh
                        if(this.refresh/1 > 0) {
                            this.timer = setInterval(() => {
                                if(this.refresh/1 > 0) {
                                    this.refresh = (this.refresh/1) - 1
                                }else  {
                                    clearInterval(this.timer)
                                }
                            }, 1000);
                        }
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
                        this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
            drawChart() {
                let myChart = this.$echarts.init(document.getElementById("main"));
                console.log(myChart)
				let option = {
					
					textStyle: {
						color: '#bbb',
					},
					grid: {
						show: true,
						top: '10%',
						// bottom: '10%',
						// right: '4%',
						left: '2.5%',
						width: '95%',
						height: '90%',
						containLabel: true,
						borderColor: '#fff',
						// shadowBlur: 10,
						// shadowColor: '#FF9234'
					},
					xAxis: {
						data: this.charts.columns,
						type:'category',
						boundaryGap: true,
						axisLine: {
                            type: 'dashed',
                            show: false,
							lineStyle : {
								color: '#E7E7E7'
							}
						},
						axisTick: {
							// alignWithLabel: true,
                            show: false,
							interval: 0,
							lineStyle: {
								color: '#E7E7E7'
							}
						},
						axisLabel: {
							// interval: 0,
						},
						splitLine: {
							show: false,
							interval: 0,
							lineStyle: {
								color: '#E7E7E7',
                                width: 0,
							}
						},
                        minorSplitLine: {
                            show: true,
                            color: 'green'
                        }
                       
                        
						
					},
					yAxis:	 {
						type: 'value',
						position: 'left',
						axisLine: {
							show: false,
							lineStyle : {
								color: '#E7E7E7'
							}
						},
						// min: 'dataMin',
						// max: 'dataMax',
						splitNumber: '4',
						// interval: '2',
						axisLabel: {
							show: true,
							interval: 2,
							// showMinLabel: false,
							// showMaxLabel:false,
						},
						axisTick: {
							show: false,
							interval: 2,
							lineStyle: {
								color: '#E7E7E7'
							}
						},
						splitLine: {
							show: true,
							interval: 2,
							lineStyle: {
                                type: 'dashed',
								color: '#E7E7E7',
                                join:'bevel'
							}
						},
						// axisPointer: {
						// 	show: true,
						// 	type: 'shadow'
						// }
						// data: this.productList.charts.values,
						// type:'category',
						// boundaryGap: true,
					},
					series: [
						{
							// name: "num",
							color: '#FF9234',
							type: "line",
							data: this.charts.values
						}
					]
				}
				myChart.setOption(option);
			},
            selectVideo(item){
                console.log(item)
                this.requireLevel = item.requireLevel
                if(this.myLevel/1 < this.requireLevel/1) {
                    this.showUpgreadAlert = true
                    return
                }

                this.selectVideoId = item.id
                this.selectVideoItem = item
                this.charts.columns = []
                this.charts.values = []
                if(item.recently.length > 0) {
                    for (let index = 0; index < item.recently.length; index++) {
                        this.charts.columns.push(item.recently[index].date)
                        this.charts.values.push(item.recently[index].earn)
                    }
                    setTimeout(() => {
                        this.drawChart()
                    }, 200);
                }
            },
            
            watchVideo(item) {
                this.playVideoUrl = item.mediaUrl
                this.showPlayVideo = true
            },
			goPage(page){
                if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
		},
        computed: {
            refreshTime(){
                if(this.refresh/1 > 0) {
                    var sec = parseInt(this.refresh);
                    var min= 0;
                    var hour= 0;
                    if(sec > 60) {
                        min= parseInt(sec/60);
                        sec = parseInt(sec%60);
                        if(min> 60) {
                            hour= parseInt(min/60);
                            min= parseInt(min%60);
                        }
                    }
                    var result = `${hour.toString().padStart(2,'0')}:${min.toString().padStart(2,'0')}:${sec.toString().padStart(2,'0')}`
                    return result;
                }else {
                    return '0'
                }
            }
        },
        destroyed(){
            if(this.timer) {
                clearInterval(this.timer)
            }
        }
	}
</script>
